import React, {useEffect, useState} from "react";
import Typography from "@material-ui/core/Typography";
import Container from "@material-ui/core/Container";
import TextField from "@material-ui/core/TextField";
import {makeStyles} from "@material-ui/core/styles";
import {setTitle} from "../controller/utils";
import {setBusy, setSnackbar} from "../controller/site";
import axios from "axios";
import {API_USER} from "../constant";
import {useDispatch} from "react-redux";
import {Box} from "@material-ui/core";
import UsersTable from "./UsersTable";

const useStyles = makeStyles((theme) => ({
    title: {
        marginTop: theme.spacing(5),
        marginBottom: theme.spacing(5),
    },
}));

export default function PeoplePage() {
    const classes = useStyles();
    const [users, setUsers] = useState([]); // 
    const [filteredUsers, setFilteredUsers] = useState([]); // 
    const [filterText, setFilterText] = useState(""); // 
    const dispatch = useDispatch();

    useEffect(() => {
        setTitle("");
        dispatch(setBusy(true));
        axios.get(API_USER, {
            withCredentials: true,
            validateStatus: status => status === 200
        })
            .then(res => res.data)
            .then(res => {
                setUsers(res["users"]);
                setFilteredUsers(res["users"]); // 
            })
            .catch(err => {
            dispatch(setSnackbar("", "error"));
        }).finally(() => dispatch(setBusy(false)));
    }, []);

    // 
    useEffect(() => {
        if (filterText.trim() === "") {
            setFilteredUsers(users); // 
        } else {
            const regex = new RegExp(filterText, "i"); // 
            const filtered = users.filter(user =>
                user && Object.values(user).some(value =>
                    typeof value === 'string' && regex.test(value)
                )
            );
            setFilteredUsers(filtered);
        }
    }, [filterText, users]);

    return (
        <Container>
            <Box display="flex" style={{width: "100%"}} flexDirection="column">
                <div className={classes.title}>
                    <Typography variant="h2"></Typography>
                </div>
                <TextField
                    label=""
                    fullWidth
                    margin="normal"
                    variant="outlined"
                    value={filterText}
                    onChange={e => setFilterText(e.target.value)}
                />
                {filteredUsers.length === 0 ? (
                    <Typography variant="body1" color="textSecondary">
                        
                    </Typography>
                ) : (
                    <UsersTable users={filteredUsers} />
                )}
            </Box>
        </Container>
    );
}
