import React from "react";
import {useDispatch, useSelector} from "react-redux";
import {setBusy, setSnackbar} from "../controller/site";
import axios from "axios";
import {API_USER} from "../constant";
import {tokenHeader} from "../controller/user";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import {tableIcons} from "../Component/MaterialTable/tableIcons";
import {localtime_exact} from "../controller/utils";
import MaterialTable from "material-table";
import {useHistory} from "react-router";
import StatusIcon from "../Component/StatusIcon/StatusIcon";

export function Tasks(props) {
    const [tasks, setTasks] = React.useState();
    const uid = useSelector(state => state.user.uid);
    const dispatch = useDispatch();
    const history = useHistory();

    React.useEffect(() => {
        dispatch(setBusy(true));
        axios.get(API_USER + "/" + uid + "/tasks", {
            headers: tokenHeader(),
            withCredentials: true,
            validateStatus: status => status === 200
        })
            .then(res => res.data)
            .then(res => {
                    setTasks(res["tasks"]);
                }
            ).catch(err => {
            dispatch(setSnackbar("", "error"));
        }).finally(() => dispatch(setBusy(false)));
    }, []);
    return <>
        <Typography variant="h5" style={{marginBottom: "2rem"}}></Typography>
        <Box display="flex" style={{width: "100%"}} flexDirection="column">
            {tasks &&
            <MaterialTable icons={tableIcons} columns={[
                {title: "", field: "mname"},
                {title: "", field: "cname"},
                {title: "", field: "name"},
                {title: "", field: "type", lookup: {
                        0: "",
                        1: "",
                        2: "",
                        3: "",
                        4: "",
                        5: "",
                        6: "",
                    }},
                {title: "", field: "last_update", render: task => localtime_exact(task["last_update"])},
                {title: "", field: "status", render: task => <StatusIcon status={task["status"]}/>, defaultSort: "asc"}
            ]} options={{
                pageSize: 20,
            }} data={tasks} components={{
                Toolbar: props => (<></>)
            }} onRowClick={(event, rowData, togglePanel) => {
                history.push("/manga/" + rowData["mid"] + "/" + rowData["cid"] + "/" + rowData["id"]);
            }} localization={{
                grouping: {
                    placeholder: "...",
                    groupedBy: ":"
                },
                pagination: {
                    labelDisplayedRows: '{from}{to}  {count}',
                    labelRowsSelect: '',
                    labelRowsPerPage: ':',
                    firstAriaLabel: '',
                    firstTooltip: '',
                    previousAriaLabel: '',
                    previousTooltip: '',
                    nextAriaLabel: '',
                    nextTooltip: '',
                    lastAriaLabel: '',
                    lastTooltip: ''
                },
            }}/>}
        </Box>
    </>
}